body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#root {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #F5F5F5;
  padding-bottom: 30%; }

.Todos {
  position: relative;
  margin: auto;
  margin-top: 5%;
  width: 35%;
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: 2px 3px 17px 0px #baafba;
  background-color: white;
  font-family: "Open Sans";
  text-align: center; }
  .Todos__top {
    -webkit-align-items: flex-start;
            align-items: flex-start;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    position: relative; }
  .Todos__title {
    text-align: center;
    font-size: 5em;
    opacity: 0.2;
    margin: 1%; }
  .Todos__input-wrapper {
    width: 90%;
    padding-bottom: 3%;
    display: -webkit-flex;
    display: flex; }
  .Todos__input {
    width: 80%;
    margin-right: 10px;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    padding: 20px 20px 20px 55px;
    border: 1px solid gray;
    border-radius: 8px;
    z-index: 3; }
  .Todos__input-button {
    width: 20%;
    border: 1px solid rgba(134, 126, 139, 0.6);
    border-radius: 3px;
    cursor: pointer; }
  .Todos__list {
    width: 100%;
    margin-left: 0;
    padding-left: 0;
    margin: 0; }
  .Todos__item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 15px;
    border-top: 1px solid rgba(134, 126, 139, 0.6); }
  .Todos__item-text {
    margin-left: 5%; }
  .Todos__bottom {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 90%;
    padding: 3px 20px 3px 20px;
    border-top: 1px solid rgba(134, 126, 139, 0.6);
    color: gray;
    font-size: 14px; }
  .Todos__bottom * {
    -webkit-align-self: center;
            align-self: center; }
  .Todos__bottom-button {
    padding: 5px 10px;
    border: 1px solid rgba(134, 126, 139, 0.6);
    border-radius: 3px;
    cursor: pointer; }
  .Todos__bottom-button * {
    padding: 2px; }

.checked {
  text-decoration: line-through; }

@media (max-width: 800px) {
  .Todos {
    margin-top: 50px;
    width: 80%; } }

